import React, {useState} from 'react'
import axios from 'axios';
import {ToastContainer,toast} from 'react-toastify'

const Contact =()=>{
	const [values, setValues] = useState({
		name:'',
		email:'',
		phone:'',
		text:''

	})
	const [loading, setLoading] = useState(false)

	const createMessage = async(msg)=>{
		return await axios.post(`http://localhost:2000/api/message`, msg)
	}

	const handleChange=(e)=>{
		setValues({...values, [e.target.name]: e.target.value})
	}

	const handleSubmit = ()=>{
		setLoading(true)
		createMessage(values).then((res)=>{
			setLoading(false)
			setValues({...values, name:'', email:'', phone:'', text:''})
			toast.success(`Thank You ${res.data.name} for contacting Bigberry...We'll get back to you within 24 hour !!`)
		}).catch((err)=>{
			setLoading(false)
			toast.error(`Your message was not sent....Please Try Again!!`)
		})
	}

    return(
        <>
            <section class="py-5" id="message">
				<div class="container" data-aos="fade-up" data-aos-duration="1100">
					<div class="row">
						<div class="col mx-auto text-center pb-3">
							<h1>
								Contact
								<span class="title-main">Us</span>
							</h1>
						</div>
					</div>
					<div class="row">
						<div class="col-10 mx-auto">
								<div class="form-group">
									<input type="text" class="form-control" value={values.name} onChange={handleChange} name="name" placeholder="Name"/>
								</div>
								<div class="form-group">
									<input type="text" class="form-control" value={values.email} name="email" onChange={handleChange} placeholder="Email"/>
								</div>
								<div class="form-group">
									<input type="text" class="form-control" value={values.phone}  name="phone" onChange={handleChange} placeholder="Mobile Number"/>
								</div>
								<div class="form-group">
									<textarea  class="form-control" name="text" value={values.text} onChange={handleChange} placeholder="Message" style={{height:'100px'}}></textarea>
								</div>
								<button class="btn btn-msg" onClick={handleSubmit} disabled={loading}>
									{loading ? 'Sending..' : 'Send Message'}
								</button>
						</div>
					</div>
				</div>
			</section>
        </>
    )
}

export default Contact;