import React, {useState} from 'react'
import OwlCarousel from 'react-owl-carousel';
import 'owl.carousel/dist/assets/owl.carousel.css';
import 'owl.carousel/dist/assets/owl.theme.default.css';
import {Card} from 'antd'

const Product = ()=>{
    const list =[
        {name: 'BERRYZYME', image:'BERRYZYME.jpg'},
        {name: 'OCIBACT DRY INJECTION', image:'OCIBACT 1GM.jpg'},
        {name: 'OCIBACT SB', image:'OCIBACT SB 500 MG.jpg'},
        {name: 'OTBIG', image:'OTBIG-n.jpg'},
        {name: 'CEFOBIG', image:'cefobig-tab.jpg'},
        {name: 'TOBABIG', image:'TOBABIG-F.jpg'},
        {name:'NC VIT-LS SYRUP', image:'ncvit.jpg'},
        {name: 'CEFONIR', image:'cefonir.jpg'},
        {name: 'NC VIT-C1', image:'nc vit-c.jpg'},
        {name: 'OCIBACT-O', image:'Ocibact-O.jpg'},
        {name:'NC VIT-GOLD', image:'NCVIT GOLD.jpg'}
    ];
    const [responsive, setResponsive] = useState({
        0: {
            items: 1,
        },
        450: {
            items: 1,
        },
        600: {
            items: 2,
        },
        1000: {
            items: 2,
        },
    },)
    const {Meta} = Card
    return(
        <>
            <section className="products py-5" id="products">
				<div className="container-fluid">
					<div className="row">
						<div className="col text-capitalize text-center pb-3">
							<h1>
							Our
							<span className="title-main pl-2">Product</span>
							</h1>
						</div>
					</div>
					<div className="row">
                        <div className="col-12 col-md-8 mx-auto my-3"  data-aos="fade-up" data-aos-duration="1100">
                            <OwlCarousel className="owl-theme" items={3} responsive={responsive} loop autoplay>
                                {list && list.map((item)=>{
                                    return(
                                        <>
                                            <Card className="mx-4" key={item.image} hoverable cover={<img src={item.image} style={{height:'350px'}}/>}>
                                                <Meta title={item.name}/>
                                            </Card>
                                        </>
                                    )
                                })}
                            </OwlCarousel>
                        </div>
                    </div>
                </div>
			</section>
        </>
    )
}

export default Product