import React from 'react'
import TypeWriter from 'typewriter-effect'

const Header = ()=>{
	const text=['YOUR HEALTH IS OUR FIRST PRIORITY..', 'WE ARE AVAILABLE 24 * 7 FOR YOUR SERVICE..']
    return(
        <>
            <div class="container-fluid height-max" id="main-head">
				<div class="container">
					<div class="row height-max align-items-center justify-content-center">
						<div class="col text-center" data-aos="zoom-in-down">
							<h1 class="text-white text-uppercase">
							<strong>BIGBERRY PHARMA</strong>
							</h1>
							<div className="text-white">
							<TypeWriter options={{
								strings:text,
								autoStart:true,
								loop:true
							}}/>
							</div>
							
							<a href="#about-us" class="btn btn-sm btn-light m-2">READ MORE</a>
						</div>
					</div>
				</div>
			</div>
        </>
    )
}

export default Header;